@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

blockquote {
  .article-content & {
    @include clearfix;
  }
}

.blockquote {
  position: relative;
  padding: 60px 15px;
  margin-left: -25px;
  margin-right: -25px;
  background: $theme-color-1;
  color: $textcolor-1;

  @include mq_min_width($tablet-small) {
    padding-left: 15px;
    padding-right: 15px;
  }

  &::before,
  &::after { // Quotes icon background
    @include icomoon;
    position: absolute;
    z-index: 0;
    display: block;
    color: $theme-color-1-dark;
  }

  &::before {
    content: '\e907';
    font-size: 60px;
    left: 20px;
    top: 20px;

    @include mq_min_width($tablet-small) {
      font-size: 80px;
    }

    @include mq_min_width($tablet-medium) {
      font-size: 100px;
      left: 40px;
      top: 40px;
    }
  }

  &::after {
    content: '\e908';
    right: 20px;
    font-size: 50px;
    top: auto;
    bottom: 20px;

    @include mq_min_width($tablet-small) {
      font-size: 60px;
    }

    @include mq_min_width($tablet-medium) {
      font-size: 80px;
      right: percentage(160px / 1360px);
      bottom: 30px;
    }
  }

  .inside {
    position: relative;
    z-index: 1;
    margin-left: percentage(130px / 1360px);
    margin-right: percentage(130px / 1360px);
    font-size: 18px;
    font-weight: 500;
    line-height: 32px;
  }

  .block-title {
    display: none;
  }
}
