.hero.hero-why-exhibit {
  height: 350px;
  z-index: 3;
  
  .hero {
    &__wrapper {
      margin-left: 0;
    }
    &__item {
      width: 85%;
      @include mq_min_width($tablet-medium) {
        width: 75%;
      }
      @include mq_min_width($desktop-medium) {
        width: 50%;
      }
    }

    &__background--with-img {
      background: $darkgrey-1;
      position: absolute;
      top: 0;
      left: 50%;
      display: block;
      width: 100vw;
      height: 100%;
      overflow: hidden;
      transform: translateX(-50%);
      z-index: -1;

      @include mq_min_width($desktop-small) {
        background: $darkgrey-1 image('visual/hero-why-exhibit-bg.png') right center / contain no-repeat;
      }
    }
  }

  h1 {
    @extend .main-title;
    color: #fff;
    text-align: left;
  }
}