body.node1310382 {
  .article-wrapper {
    position: relative;
    .article {
      &-title {
        > .inside {
          .at-content .at-main-title {
            @extend .main-title;
            border: 0; 
            margin-top: 20px;
            margin-bottom: 51px;

            @include mq_min_width($desktop-medium) {
              font-size: 42px;
              line-height: 1.5;
            }
          }
          
          @include mq_min_width($desktop-small) {          
            img.cx-lazy-load {
              width: 823px;
              height: 382px;
            }
          }
        }
        .at-content {
          width: 100%;
        }
      }

      &-intro {
        display: block;
        width: 100%;
        position: absolute;
        top: 155px;
        left: 0;

        p {
          color: $lightgrey-2;
          width: 50%;
          margin: auto;
          font-size: 20px;
          font-weight: 600;
          line-height: 31px;
          text-align: center;
        }
      }

      &-content {
        position: relative;
        margin-top: -150px;
        margin-bottom: 80px;
        padding: 225px 15px 80px;

        &::before {
          content: '';
          display: block;
          position: absolute;
          top: 0;
          left: -36px;
          width: 100vw;
          height: 100%;
          background-color: $lightgrey-3;
          z-index: -1;
        }

        h2, 
        h3, 
        h4 {
          @extend .main-title;
        }

        h2 {
          font-size: 40px;
          line-height: 1.5;
          margin-bottom: 16px;
        }

        h3,
        h4 {
          color: $textcolor-2;
        }

        h3 {
          font-size: 25px;
          line-height: 1.6;
          margin-top: 15px;
          margin-bottom: 20px;
        }

        h4 {
          font-size: 20px;
          line-height: 2;
          margin-top: 15px;
          margin-bottom: 15px;
        }

        > * {
          margin: auto;
          width: 90%;

          @include mq_min_width($desktop-small) {
            width: 66.6667%;
            min-width: 823px;
          }
        }

        p {
          margin-top: 15px;
          margin-bottom: 15px;
        }

        ul {
          li {
            &::before {
              border-radius: 50%;
              background: $textcolor-2;
            }
          }
        }

        blockquote {
          @extend .blockquote;
          margin: 36px auto 41px;

          @include mq_min_width($desktop-small) {
            padding: 68px 56px 69px 88px;
          }

          p {
            color: $textcolor-2;
            font-family: $font-2;
            font-size: 25px;
            font-weight: 500;
            line-height: 1.68;
            border: 0;
            position: relative;
            z-index: 1; 
          }

          i {
            display: none;
          }
        }

        iframe {
          display: block;
          max-width: 823px;
          margin-top: 40px;
          margin-bottom: 46px;
        }
      }
    }

    .social-sharing {
      display: flex;
      margin: 20px auto 50px;

      > .inside {
        margin: auto;
      }
      .social-sharing {
        margin: 0 auto;
      }

      @include mq_min_width($desktop-medium) {
        transform: none;
        display: block;
        position: absolute;
        left: -23px;
        top: 95px;
        border: 0;
        margin-top: 0;
        background: none;
      }
    }
  }
}